export default {
	data() {
		return {
				biId:'',
				content:'',
				authKey:'',
				free:''
			};
		},
	onLoad(e) {
		this.authKey = uni.getStorageSync('authKey');
		this.biId = e.id;
		this.getListDetail();
		console.log(e.id)
	},
	methods:{
		// 获取服务详情
		getListDetail(){
			var that = this;
			uni.request({
				url: this.http+'order/detail/' + this.biId,
				method: 'GET',
				header: {
					authKey: this.authKey
				},
				data: {},
				success: res => {
					console.log(res.data)
					if (res.data.code == 200) {
						let content = res.data.data
						content.product.cover = that.static + content.product.cover
						that.content = content
						that.free = content.sku.fee_reduce_count
					}
				},
			});
			// uni.request({
			// 	url: this.http+'home/reduce',
			// 	method: 'GET',
			// 	header: {
			// 		authKey: this.authKey
			// 	},
			// 	data: {},
			// 	success: res => {
			// 		console.log(res.data)
			// 		if (res.data.code == 200) {
			// 			this.free = res.data.data
			// 		}
			// 	},
			// });
		},
		copy(e){
			uni.setClipboardData({
	            data: e,
	            success: function () {
	                uni.showToast({
	                	title:'复制成功',
	                	icon:'none',
	                	duration:1500
	                })
	            }
	        });
		},
		detail(){
			uni.navigateTo({
				url: '/pages/groupSuccess/spellGroup?id=' + this.biId
			});
		}
	}
}
